@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{padding:0;margin:0;box-sizing:border-box;letter-spacing:1px;font-family:"Roboto", sans-serif;outline:none}.chart-container{display:flex;justify-content:center}.cards-container{grid-gap:1rem !important;margin-bottom:3rem}.cards-container .cards-title{text-align:center;font-weight:bold}.cards-container .card-infected{border-bottom:5px solid rgba(0,0,255,0.5);margin-right:1rem !important}.cards-container .card-recovered{border-bottom:5px solid rgba(0,255,0,0.5);margin-right:1rem !important}.cards-container .card-deaths{border-bottom:5px solid rgba(255,0,0,0.5)}.form-control{width:100%;text-align:center;margin:3rem}.form-control .select-country{width:80%;margin:3rem auto}@media screen and (min-width: 768px){.chart-container{width:90%;margin:1rem auto}}

