@import "config";

.chart-container {
    display: flex;
    justify-content: center;
}
.cards-container {
    grid-gap: 1rem !important;
    margin-bottom: 3rem;
    .cards-title {
        text-align: center;
        font-weight: bold;
    }
    .card-infected {
        border-bottom: 5px solid $blue;
        margin-right: 1rem !important;
    }
    .card-recovered {
        border-bottom: 5px solid $green;
        margin-right: 1rem !important;
    }
    .card-deaths {
        border-bottom: 5px solid $red;
    }
}
.form-control {
    width: 100%;
    text-align: center;
    margin: 3rem;
    .select-country {
        width: 80%;
        margin: 3rem auto;
    }
}

@media screen and (min-width: 768px) {
    .chart-container {
        width: 90%;
        margin: 1rem auto;
    }
}
